import React, {forwardRef, useImperativeHandle, useState, useEffect } from "react";
import styled from '@emotion/styled'


const Rando = (props) => {

    const[inputArray, setInputArray] = useState(props.data);
    const[returnArray, setReturnArray] = useState();
    const date = new Date().toLocaleString()

    const Meta = styled.div`
        width: 100%;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        margin-top: 10px;

        a {
            color: #fff;
        }
    `
    const RondoContainer = styled.div`
        background-color: #333;
        border-radius:5px;
        width: 92%;
        padding: 16px 4%;
        margin: 10px 0;
        overflow: hidden;
        font-size: 1.2em;
        min-height: 110px;
    `

    useEffect(()=> {
        
        if(inputArray !== undefined) {
            shuffleArray(inputArray)
        }

    },[inputArray]);



    function shuffleArray(array) {
        for(let i = array.length - 1; i > 0; i--) {
            const j = Math.floor(Math.random() * (i + 1));
            [array[i], array[j]] = [array[j], array[i]];
        }

        setReturnArray(array)
    }
    
    return (
        <>
        <RondoContainer>
            <ol>
                {returnArray &&
                    returnArray.map(item =>
                        <li key={item}>{item}</li>
                    )   
                }
            </ol>
            
        </RondoContainer>
        <Meta>
            <small>Last Shuffle: {date}</small>
            <small>Built with ‚ù§Ô∏è by <a href="">r2</a>, üìç Chicago, ILÛ†ÅµÛ†Å≥</small>
        </Meta>
        </>

    );


    
}

export default Rando;
